.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}

.header__in {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.slider {
  position: relative;
}

.slider__btn {
  position: absolute;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  padding: 20px;
  overflow: hidden;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.4s ease;
}

.slider__btn--next {
  left: auto;
  right: 50px;
}

.slider__btn--prev {
  transform: translateY(-50%) rotate(180deg);
}

.slider__btn svg {
  font-size: 45px;
  fill: #87bee7;
  transition: all 0.4s ease;
}

.slider__btn:hover {
  background-color: rgba(239, 135, 238, 0.6);
}

.slider__btn:hover svg {
  fill: #0398cf;
}

.slide {
  opacity: 0.5;
  height: 600px;
  display: flex;
  margin: 40px;
  transition: all 0.4s ease;
}

.slide img {
  width: 100%;
  object-fit: cover;
  object-position: top center;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 10px -5px #000000;
}

.slick-center .slide {
  opacity: 1;
  transform: scale(1.2);
}

.hero {
  height: 100vh;
  box-shadow: 0px 4px 15px;
}

.hero__in {
  position: relative;
  height: 100%;
  width: 100%;
}

.hero__img {
  display: flex;
  height: 100%;
}

.hero__img img {
  width: 100%;
  object-fit: cover;
  object-position: center -150px;
  opacity: 0.5;
}

.hero__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  width: 1240px;
  font-size: 56px;
  font-family: 'Architects Daughter', cursive;
  font-weight: 400;
  color: white;
  z-index: 2;
}

.hero__title span:last-child {
  margin-left: auto;
  margin-right: 30px;
  margin-top: 20px;
  font-size: 22px;
  opacity: 1;
}

.hero__overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(0, 41, 64, 0.7);
}

.about__wrapper {
  display: flex;
  margin-bottom: 40px;
}

.about__wrapper--white {
  padding: 40px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__text p {
  margin-bottom: 20px;
  font-size: 18px;
}

.about__img {
  display: flex;
  margin-right: 40px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__img img {
  border-radius: 5px;
  object-fit: cover;
}

.about__subtitle {
  text-align: center;
  font-weight: 400;
  font-family: 'Julius Sans One', sans-serif;
  font-size: 32px;
  margin-bottom: 20px;
}

.about__col {
  padding: 40px;
  margin-right: 40px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__col:last-child {
  margin-right: 0;
}

.about__list li {
  margin-bottom: 10px;
}

.insta__in {
  display: flex;
}

.insta__imgs {
  display: flex;
  margin-left: 100px;
}

.insta__img {
  display: flex;
  width: 300px;
  height: 500px;
  margin-right: 20px;
  background-color: #FFF;
  border-radius: 5px;
  box-shadow: 0 2px 10px -5px #000000;
  overflow: hidden;
  z-index: 1;
}

.insta__img:last-child {
  margin-right: 0;
}

.insta__img img {
  width: 100%;
  object-fit: cover;
  animation-name: example;
  animation-duration: 40s;
  animation-iteration-count: infinite;
}

.insta__text {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 500px;
  min-width: 700px;
  margin-left: -100px;
  margin-right: 200px;
  margin-top: 100px;
  padding: 40px;
  padding-left: 140px;
  font-family: 'Architects Daughter', cursive;
  font-size: 20px;
  background-color: #FFF;
  border-radius: 5px;
  box-shadow: 0 2px 10px -5px #000000;
}

.insta__text p {
  margin-bottom: 20px;
}

@keyframes example {
  0% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

.contact {
  margin-bottom: 80px;
  font-family: 'Julius Sans One', sans-serif;
}

.contact__in {
  display: flex;
  padding: 40px;
  background-color: #FFF;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.contact__photo {
  display: flex;
  width: 420px;
  margin-right: 80px;
}

.contact__photo img {
  width: 100%;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.contact__name {
  font-size: 24px;
  letter-spacing: 10px;
  margin-bottom: 20px;
}

.contact__subtitle {
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

.contact__phone {
  display: flex;
  align-items: center;
  color: #4c4c4c;
  text-decoration: none;
  font-family: 'Julius Sans One', sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.contact__phone:hover svg {
  transform: scale(1.2);
}

.contact__location {
  display: flex;
  align-items: center;
  font-weight: 700;
  cursor: pointer;
}

.contact__location:hover svg {
  transform: scale(1.2);
}

.contact__inner {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}

.contact__inner label {
  width: 50%;
}

.contact__wrap {
  margin-right: 40px;
}

.contact svg {
  width: 34px;
  height: 34px;
  margin-right: 20px;
  transition: transform 0.4s ease;
}

.contact__form {
  display: flex;
  flex-direction: column;
}

.contact__form label {
  display: flex;
  flex-direction: column;
}

.contact__form label:first-child {
  margin-right: 20px;
}

.contact__form label span {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 5px;
}

.contact__form input {
  padding: 10px 20px;
  border: 2px solid #009fe8;
  border-radius: 5px;
  font-size: 18px;
  font-family: 'Julius Sans One', 'Jura', sans-serif;
  font-weight: 700;
}

.contact__form textarea {
  resize: none;
  padding: 10px 20px;
  border: 2px solid #009fe8;
  border-radius: 5px;
  font-family: 'Julius Sans One', 'Jura', sans-serif;
  font-weight: 700;
  height: 180px;
  margin-bottom: 20px;
}

.contact__form button.btn {
  margin-left: auto;
  padding: 15px 30px;
  font-size: 20px;
  font-family: 'Julius Sans One', sans-serif;
  font-weight: 700;
  background-color: #009fe8;
  border-radius: 5px;
  color: white;
}
